import React from 'react';
import { Container } from 'reactstrap';
import Slider from "react-slick";

// images 
import ArrowRight from '../assets/images/arrowright.svg';
import Person from '../assets/images/photo.png';
import Star from '../assets/images/star.svg';

function ClientFeedback(){
    // slick slider settings 
    const settings = {
        autoplay: true,
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1
    };
    return(
        <div className="clientfeedback" id="ourclient">
            <Container>
                <div className="d-flex justify-content-between flexDiv">
                    <div className="leftone" data-aos="fade-right">
                        <div className="commonTitle">Our Clients Feedback</div>
                        <div className="commonDesc">It is a long established fact that a reader will be distracted by the readable content of a page when looking at its layout. The point of using Lorem Ipsum is</div>
                    </div> 
                    <div className="rightone text-right" data-aos="fade-left">
                        <button>Read More <img src={ArrowRight} alt=""/></button>
                    </div>
                </div>
                {/* slick slides  */}
                <div className="slickslide" data-aos="fade-right" data-aos-duration="2000">
                    <Slider {...settings}>
                        <div className="client d-flex">
                            <img className="personImg" src={Person} alt=""/>
                            <div className="rightSide">
                                <div className="stars">
                                    <img src={Star} alt=""/>
                                    <img src={Star} alt=""/>
                                    <img src={Star} alt=""/>
                                    <img src={Star} alt=""/>
                                    <img src={Star} alt=""/>
                                </div>
                                <q className="quotation">
                                    Sed ut qerspiciatis unde omnis iste natus error sit voluptatem accusantium doloremque laudantium, totam rem aperiam, eaque ipsa quae ab illo modi tem.
                                </q>
                                <div className="person">
                                    Riad Isalm
                                </div>
                                <div className="post">
                                    Product Manager. <a href="https://pixabay.com">@pixbay</a>
                                </div>
                            </div>
                        </div>
                        <div className="client d-flex">
                            <img className="personImg" src={Person} alt=""/>
                            <div className="rightSide">
                                <div className="stars">
                                    <img src={Star} alt=""/>
                                    <img src={Star} alt=""/>
                                    <img src={Star} alt=""/>
                                    <img src={Star} alt=""/>
                                    <img src={Star} alt=""/>
                                </div>
                                <q className="quotation">
                                    Sed ut qerspiciatis unde omnis iste natus error sit voluptatem accusantium doloremque laudantium, totam rem aperiam, eaque ipsa quae ab illo modi tem.
                                </q>
                                <div className="person">
                                    Riad Isalm
                                </div>
                                <div className="post">
                                    Product Manager. <a href="https://pixabay.com">@pixbay</a>
                                </div>
                            </div>
                        </div>
                    </Slider>
                </div>
            </Container>
        </div>
    );
}

export default ClientFeedback;