import React from 'react';
import { Container, Row, Col } from 'reactstrap';

// images 
import uniqueApp from '../assets/images/uniqueapp.svg';
import byConstruction from '../assets/images/byConstruction.svg';

function Service(){
    // services cards 
    const cards = [
        {
            image: uniqueApp,
            title: "Unique App Ui",
            desc: "It is a long established fact that a reader will be distracted by the readable content of a page",
            animation: "fade-right"
        },
        {
            image: uniqueApp,
            title: "excellent dashboard",
            desc: "It is a long established fact that a reader will be distracted by the readable content of a page",
            animation: "fade-up"
        },
        {
            image: byConstruction,
            title: "by construction",
            desc: "It is a long established fact that a reader will be distracted by the readable content of a page",
            animation: "fade-left"
        },
    ];
    return(
        <div className="services" id="services">
            <Container className="text-center">
                <Row>
                    <Col md="3" sm="12"></Col>
                    <Col md="6" sm="12" data-aos="fade-down">
                        {/* service title  */}
                        <div className="commonTitle">
                            We provide various kind of service for you
                        </div>
                        <div className="commonDesc">
                            It is a long established fact that a reader will be distracted by the readable content of a page when looking at its layout. The point of using Lorem.
                        </div>
                    </Col>
                    <Col md="3" sm="12"></Col>
                </Row>
                <Row>
                    {cards.map((value, index) => {
                        return <Col md="4" sm="12" key={index}>
                            <div className="cardApp" data-aos={value.animation}>
                                <div className="image">
                                    <img src={value.image} alt=""/>
                                </div>
                                <div className="title">{value.title}</div>
                                <p>{value.desc}</p>
                            </div>
                        </Col>
                    })}
                </Row>
            </Container>
        </div>
    );
}

export default Service;