import React from 'react';
import { Container, Row, Col } from 'reactstrap';

// images 
import iPhone1 from '../assets/images/iphone1.png';
import iPhone2 from '../assets/images/iphone2.png';
import iPhone3 from '../assets/images/iphone3.png';

function Portfolio(){
    return(
        <div className="portfolio" id="ourportfolio">
            <Container className="text-center">
                <Row>
                    <Col md="3" sm="12"></Col>
                    {/* portfolio title  */}
                    <Col md="6" sm="12" data-aos="fade-down">
                        <div className="commonTitle">
                            Our Portfolio
                        </div>
                        <div className="commonDesc">
                            It is a long established fact that a reader will be distracted by the readable content of a page when looking at its layout. The point of using Lorem.
                        </div>
                    </Col>
                    <Col md="3" sm="12"></Col>
                </Row>

                {/* phones  */}
                <Row>
                    <Col md="3" sm="12" data-aos="fade-right">
                        <img className="portfolioImg" src={iPhone1} alt=""/>
                    </Col>
                    <Col md="3" sm="12" data-aos="fade-down">
                        <img className="portfolioImg" src={iPhone2} alt=""/>
                    </Col>
                    <Col md="3" sm="12" data-aos="fade-up">
                        <img className="portfolioImg" src={iPhone3} alt=""/>
                    </Col>
                    <Col md="3" sm="12" data-aos="fade-left">
                        <img className="portfolioImg" src={iPhone3} alt=""/>
                    </Col>
                </Row>
                <div className="startTrip">
                    {/* circles  */}
                    <div className="upcircle"></div>
                    <div className="downcircle"></div>
                </div>
            </Container>
        </div>
    );
}

export default Portfolio;