import React, { useState } from 'react';
import iPhone1 from '../assets/images/iphone1.png';
import iPhone2 from '../assets/images/iphone2.png';
import iPhone3 from '../assets/images/iphone3.png';
import Home from '../assets/images/home.svg';
import Download from '../assets/images/download.svg';
import Arrowright from '../assets/images/whitearrow.svg';
import { Container , Row, Col, NavbarToggler, Collapse } from 'reactstrap';
import { Link, animateScroll as scroll } from "react-scroll";

function Header(){
    const [collapsed, setCollapsed] = useState(true);

    const toggleNavbar = () => setCollapsed(!collapsed);

    return(
        <div>
            <div className="header">
                <Container>
                    {/* Navigation menu */}
                    <nav className="navbar navbar-expand-lg navbar-light" data-aos="fade-down">
                        <a className="navbar-brand" href="#home"> <img width="40px" src={Home} alt=""/> <span>Apps</span> Market</a>

                        <NavbarToggler onClick={toggleNavbar} className="mr-2" />
                        {/* Links  */}
                        <Collapse isOpen={!collapsed} navbar>
                            <ul className="navbar-nav ml-auto mt-2 mt-lg    -0">
                                <li className="nav-item active">
                                    <Link
                                        className="nav-link"
                                        activeClass="active"
                                        to="home"
                                        spy={true}
                                        smooth={true}
                                        offset={-70}
                                        duration= {500}
                                   >Home</Link>
                                </li>
                                <li className="nav-item">
                                    <Link
                                        className="nav-link"
                                        activeClass="active"
                                        to="services"
                                        spy={true}
                                        smooth={true}
                                        offset={-70}
                                        duration= {500}
                                   >Services Us</Link>
                                </li>
                                <li className="nav-item">
                                    <Link
                                        className="nav-link"
                                        activeClass="active"
                                        to="whyus"
                                        spy={true}
                                        smooth={true}
                                        offset={-70}
                                        duration= {500}
                                   >Why Us</Link>
                                </li>
                                <li className="nav-item">
                                    <Link
                                        className="nav-link"
                                        activeClass="active"
                                        to="ourgoals"
                                        spy={true}
                                        smooth={true}
                                        offset={-70}
                                        duration= {500}
                                   >Our Goals</Link>
                                </li>
                                <li className="nav-item">
                                <Link
                                        className="nav-link"
                                        activeClass="active"
                                        to="ourportfolio"
                                        spy={true}
                                        smooth={true}
                                        offset={-70}
                                        duration= {500}
                                   >Our Portfolio</Link>
                                </li>
                                <li className="nav-item">
                                    <Link
                                        className="nav-link"
                                        activeClass="active"
                                        to="ourclient"
                                        spy={true}
                                        smooth={true}
                                        offset={-70}
                                        duration= {500}
                                   >Our Client</Link>
                                </li>
                                <li className="nav-item">
                                    <Link
                                        className="nav-link"
                                        activeClass="active"
                                        to="contactus"
                                        spy={true}
                                        smooth={true}
                                        offset={-70}
                                        duration= {500}
                                   >Contact Us</Link>
                                </li>
                            </ul>
                        </Collapse>
                    </nav>
                    
                    {/* header files  */}
                    <div className="headDescript">
                        <Row className="align-items-center">
                            <Col md="4" sm="12">
                                <div className="desc" data-aos="fade-right">
                                    <h2>we create applications with excellent technology.</h2>
                                    <div className="innerdiv">
                                        It is a long established fact that a reader will be distracted by the readable content of a page when looking at its layout. The point of using Lorem Ipsum is that it has a more-or-less normal distribution of letters, as opposed to using 'Content 
                                    </div>
                                    <div>
                                        <button className="downloadBtn">Download Now <img src={Download} alt=""/></button>
                                        <a href="#ourportfolio" className="exploreBtn">Explore More <img src={Arrowright} alt=""/></a>
                                    </div>
                                </div>
                            </Col>
                            <Col md="8" sm="12">
                                <div className="mobilePhone" data-aos="fade-left">
                                    <img className="smallOne" src={iPhone1} alt=""/>
                                    <img className="bigOne" src={iPhone2} alt=""/>
                                    <img className="smallOne" src={iPhone3} alt=""/>
                                </div>
                            </Col>
                        </Row>
                    </div>
                    
                </Container>
            </div>
        </div>
    );
}

export default Header;